.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  margin: 0 0 10px;
  padding: 0;
}

.react-tabs__tab {
  display: inline-block;
  border: 1px solid transparent;
  border-bottom: none;
  border-radius: 0.25rem;
  bottom: -1px;
  position: relative;
  list-style: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  color: #007bff;
}

.react-tabs__tab--selected {
  background: #007bff;
  color: white;
}

.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
  display: none;
}

.react-tabs__tab:focus {
  box-shadow: 0 0 5px #0188fe;
  border-color: #0188fe;
  outline: none;
}

.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #fff;
}

.react-tabs__tab:hover {
  border: 1px solid #007bff;
  background-color: #e0f0ff;
  color: #007bff;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}
.has-error {
  border-color: red;
  background-color: #fff0e8;
}

.has-error:focus {
  color: red;
  border-color: red;
}

.card-body {
  margin-top: -15px;
}

.card-panel {
  background-color: transparent;
}

.small .btn-sm {
  padding: .2rem .4rem;
  font-size: .75rem;
  line-height: 1.25;
  border-radius: .25rem;
}

.form-check-right {
  margin-right: 1.1rem;
}

.form-check-right input {
  margin-left: 0.5rem;
}

.field-required {
  font-style: italic;
  color: red;
  font-size: smaller;
}
.toggleRight {
  float: right;
  margin-left: 5px;
}

.toggleLeft {
  float: left;
  margin-right: 5px;
}

.toggle-label .control-label {
  width: 100%;
}
